* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

#root {
  height: 100%;
}

html,
body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0%;
  padding: 0%;
  min-height: 10vh;
  height: 100%;
}

.container {
  height: 100%;
  transition: background-color 0.25s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card {
  background: linear-gradient(
    125.66deg,
    #8470ff 11.48%,
    #8470ff 37.48%,
    #8470ff 45.48%,
    #8470ff 54.48%,
    #8470ff 63.48%,
    #8470ff 75.48%,
    #8470ff 82.48%,
    #8470ff 93.48%
  );
  box-shadow: inset 0px 4px 15px rgba(0, 0, 0, 00.25);
  width: 80%;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  flex-direction: column;
}
.header {
  margin-bottom: 10px;
  font-size: 20px;
  color: white;
}
span {
  margin-bottom: 10px;
}
.input {
  width: 70%;
  height: 25px;
  outline: none;
  border: none;
  font-size: 15px;
  border-radius: 10px;
  padding-left: 20px;
}
